import React, { Component } from "react";
import { Link } from "react-router-dom";
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import Background from "../../../assets/images/background.jpg";
import api from "../../../api";

import md5 from "md5";
import { ScaleLoader } from "react-spinners";
import { css } from "@emotion/core";
const override = css`
  display: block;
  border-color: black;
  position: absolute;
  left: 44%;
  top: 50%;
  z-index: 1;
  position: fixed;
`;

class Login extends Component {
  constructor(props) {
    super(props);

    this.handleChange = this.handleChange.bind(this);
    this.loginSubmit = this.loginSubmit.bind(this);

    this.state = {
      username: "",
      password: "",

      loginError: "",

      usernameState: false,
      passwordState: false,

      //spinner
      loading: false,
    };
  }

  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
    this.setState({
      loginError: "",
      passwordState: false,
      usernameState: false,
    });
  };

  loginSubmit = (e) => {
    if (this.state.username === "") {
      this.setState({ loginError: "Enter Email!!!", usernameState: true });
    }
    if (this.state.password === "") {
      this.setState({ loginError: "Enter Password!!!", passwordState: true });
    }

    if (this.state.password === "" && this.state.username === "") {
      this.setState({
        loginError: "Enter Email & Password!!!",
        usernameState: true,
        passwordState: true,
      });
    }

    if (this.state.username !== "" && this.state.password !== "") {
      var login_data = {
        username: this.state.username,
        password: md5(this.state.password),
      };

      this.setState({ loading: true });
      fetch(api.apiUrl + "login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        // headers: authHeader(),
        body: JSON.stringify(login_data),
      })
        .then((response) => response.json())
        .then((responseJsonFromServerData) => {
          if (responseJsonFromServerData.accessToken) {
            this.setState({ loading: false });
            localStorage.setItem(
              "user",
              JSON.stringify(responseJsonFromServerData)
            );
            if (parseInt(responseJsonFromServerData.roles) === 2) {
              this.props.history.push("/service-provider-dashboard");
            }
            if (parseInt(responseJsonFromServerData.roles) === 1) {
              this.props.history.push("/admin-dashboard");
            }
          } else {
            this.setState({
              loginError: "Email/Password is incorrect!",
              loading: false,
              usernameState: true,
              passwordState: true,
            });
          }
        })
        .catch((error) => {
          console.error(error);
          this.setState({ loading: false });
        });
    }
  };

  render() {
    return (
      <div>
        <ScaleLoader
          height={40}
          width={12}
          css={override}
          size={150}
          //size={"150px"} this also works
          color={"#B54545"}
          loading={this.state.loading}
        />
        <div
          className="c-app c-default-layout flex-row align-items-center"
          style={{
            backgroundImage: `url(${Background})`,
            backgroundPosition: "center",
            backgroundRepeat: "no-repeat",
            backgroundSize: "cover",
          }}
        >
          <CContainer>
            <CRow className="justify-content-center">
              <CCol md="8">
                <CCardGroup>
                  <CCard className="p-4">
                    <CCardBody>
                      <CForm>
                        <h2>Welcome to Mechgiri</h2>
                        <p className="text-muted">Sign In to your account</p>
                        <CInputGroup className="mb-3">
                          <CInputGroupPrepend>
                            <CInputGroupText>
                              <CIcon name="cil-user" />
                            </CInputGroupText>
                          </CInputGroupPrepend>
                          <CInput
                            autoFocus
                            type="text"
                            placeholder="Email"
                            autoComplete="username"
                            onChange={this.handleChange("username")}
                            style={{
                              borderBottomColor:
                                this.state.usernameState === true ? "red" : "",
                              borderTopColor:
                                this.state.usernameState === true ? "red" : "",
                              borderRightColor:
                                this.state.usernameState === true ? "red" : "",
                            }}
                          />
                        </CInputGroup>
                        <CInputGroup className="mb-4">
                          <CInputGroupPrepend>
                            <CInputGroupText>
                              <CIcon name="cil-lock-locked" />
                            </CInputGroupText>
                          </CInputGroupPrepend>
                          <CInput
                            type="password"
                            placeholder="Password"
                            autoComplete="current-password"
                            onChange={this.handleChange("password")}
                            style={{
                              borderBottomColor:
                                this.state.passwordState === true ? "red" : "",
                              borderTopColor:
                                this.state.passwordState === true ? "red" : "",
                              borderRightColor:
                                this.state.passwordState === true ? "red" : "",
                            }}
                          />
                        </CInputGroup>
                        <p
                          className="error"
                          style={{
                            display:
                              this.state.loginError === "" ? "none" : "block",
                          }}
                        >
                          <i className="fa fa-exclamation-triangle"></i>{" "}
                          {this.state.loginError}
                        </p>
                        <CRow>
                          <CCol xs="6">
                            <CButton
                              className="px-4 main-btn"
                              onClick={this.loginSubmit}
                            >
                              Login
                            </CButton>
                          </CCol>
                          <CCol xs="6" className="text-right">
                            <Link to="/forgotpassword">
                              <CButton color="link" className="px-0">
                                Forgot password?
                              </CButton>
                            </Link>
                          </CCol>
                        </CRow>
                      </CForm>
                      <p
                        id="reg-link"
                        style={{ display: "none", marginTop: "10px" }}
                      >
                        Not a memeber yet ?{" "}
                        <Link to="/register">Register here</Link>
                      </p>
                    </CCardBody>
                  </CCard>
                  <CCard
                    className="text-white  py-5 d-md-down-none main-color"
                    style={{ width: "44%" }}
                  >
                    <CCardBody className="text-center">
                      <div>
                        <h2>Sign up</h2>
                        <p>To join the mechgiri network sign up here!!!</p>
                        <Link to="/register">
                          <CButton
                            className="mt-3 main-reverse"
                            active
                            tabIndex={-1}
                          >
                            Register Now!
                          </CButton>
                        </Link>
                      </div>
                    </CCardBody>
                  </CCard>
                </CCardGroup>
              </CCol>
            </CRow>
          </CContainer>
        </div>
      </div>
    );
  }
}

export default Login;
